import { Link, Route, Switch } from 'react-router-dom'
import HomePage from "./pages/HomePage";
import JotForm from "./pages/JotForm";
import Cards from "./pages/Cards";
import { Navbar, Nav } from "react-bootstrap";
import 'fontsource-roboto';
import React from "react";

function App() {
    return (
            <div>
                <header className="header">
                    <br />
                    <div className="container">
                        <Navbar bg="dark" variant="dark" className="navbar_border-radius" >
                            <Navbar.Brand href="/">Mijn portal</Navbar.Brand>
                            <Nav className="mr-auto">
                                {/*<Nav.Link href="/">Home</Nav.Link>*/}
                                <Nav.Link as={Link} to="/new">Nieuwe aanvraag</Nav.Link>
                                <Nav.Link as={Link} to="/check">Status mijn dossiers</Nav.Link>
                            </Nav>
                        </Navbar>
                    </div>
                </header>
                <div className="container">
                    <img className="img-fluid mb-4" src="logo.png" alt="" />
                    <Switch>
                        <Route exact path="/" component={HomePage} />
                        <Route path="/new" component={JotForm} />
                        <Route path="/check" component={Cards} />
                    </Switch>
                    <p className="mt-5 mb-3 text-muted footer">Electrotest &copy; 2021</p>
                    {/*<div className="container mt-5">*/}
                    {/*  <div className="card-columns">*/}
                    {/*    {this.state.movies.filter(movie => movie.fields.Bron == "Malines Group").map(movie => <Cards {...movie.fields} /> )}*/}
                    {/*  </div>*/}
                </div>
            </div>
    );
}

export default App;

